.detail-event{padding:2em;min-height:100vh}.detail-event .title{text-align:center;font-family:"Rock Salt", cursive;font-weight:bold;font-size:42px;-webkit-animation:growIn 2s;animation:growIn 2s}.detail-event .section-detail-event .akad{margin-top:50px;text-align:center}.detail-event .section-detail-event .akad .text{font-size:24px;font-weight:500}.detail-event .section-detail-event .akad .date{letter-spacing:0.5px}.detail-event .section-detail-event .akad .time{font-size:14px;letter-spacing:0.5px}.detail-event .section-detail-event .resepsi{margin-top:25px;text-align:center}.detail-event .section-detail-event .resepsi .text{font-size:24px;font-weight:500}.detail-event .section-detail-event .resepsi .sesi{font-size:14px;letter-spacing:0.5px}.detail-event .section-detail-event .place{margin-top:25px;text-align:center}.detail-event .section-detail-event .place .text{font-size:24px;font-weight:500}.detail-event .section-detail-event .place .address{font-size:14px;letter-spacing:0.5px}.detail-event .section-detail-event .button{margin-top:25px}.detail-event .section-detail-event .button .btn-primary{padding:5px 15px;cursor:pointer;letter-spacing:2px;background-color:#968984;border-color:#968984}@-webkit-keyframes growIn{0%{-webkit-transform:scale(0.2);transform:scale(0.2);opacity:0}50%{-webkit-transform:scale(1.2);transform:scale(1.2)}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}@keyframes growIn{0%{-webkit-transform:scale(0.2);transform:scale(0.2);opacity:0}50%{-webkit-transform:scale(1.2);transform:scale(1.2)}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}

